.maindiv {
    margin-top: 25px;
    display: flex;
    flex-direction: row;
}
.mainBox {
    margin-right: auto;
    margin-left: auto;
    display: grid;
    grid-gap: 10px;
    width: 85%;
    min-width: 50%;
    overflow: auto;
    height: 90vh;
    text-align: center;
    
}
.inner1 {
    grid-column: 1 / span 2;
    grid-row: 1/span 2;
       
}
.inner2 {
    grid-column: 3 / span 3;
    grid-row: 1 /span 3;
}
.inner3 {
    grid-column: 1 / span 2;
    grid-row: 3/span 2;
}
.inner4 {
    grid-column: 3 / span 1;
    grid-row: 4/span 1;
}
.inner5 {
    grid-column: 4 / span 2;
    grid-row: 4/span 3;
}
.inner6 {
    grid-column: 1 / span 3;
    grid-row: 5/span 2;
}
.inner7 {
    grid-column: 1 / span 5;
    grid-row: 7 / span 2;
}
.mainBox > div {
    background-color: rgba(170, 108, 199, 0.8);
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    padding: 20px 0;
}
