.logo {
    display: flex;
    margin-left: 10px;
    background-color: rgb(143, 238, 233);
    width: 100vw;
    padding: 5px;
    font-size: 30px;
    font-weight: bold;
}
.navbar {
    text-decoration: none;
    width: 100vw;
    padding:10px;
    display: flex;
    background-color: rgb(143, 238, 233);
}
.icons {
    align-items: center;
    justify-content: center;
    position: absolute;
    right: 0;
    justify-content: space-evenly;
    padding: 5px;
}
.assignIcon {
    margin-right: 20px;
    color:black;
}
.addIcon {
    margin-right: 20px;
    color:black;
}
.accountIcon {
    margin-right: 20px;
    color:black;
}
.notificationIcon {
    margin-right: 20px;
    color:black;
}
.assignIcon:hover {
   
    color:white;
}
.addIcon :hover {
    
    color:white;
}
.accountIcon :hover {
    
    color:white;
}
.notificationIcon :hover {
   
    color:white;
